import {Component, Input, ChangeDetectionStrategy, ChangeDetectorRef} from '@angular/core';
import {NglConfig, NglConfigurable} from '../config/config';


@NglConfigurable()
export class NglIconSvg {

   category: string = 'utility';
   icon: string;
   xPos: string = '0';

  constructor(private config: NglConfig, private cd: ChangeDetectorRef) {}

  iconPath() {
    return `${this.config.get('svgPath')}/${this.category}-sprite/svg/symbols.svg#${this.icon}`;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'svg[nglIcon]',
  template: `
    <svg:use [attr.xlink:href]="iconPath()" [attr.x]="xPos"></svg:use>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[attr.aria-hidden]': 'true',
  },
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: NglConfig, },
{type: ChangeDetectorRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'category': [{ type: Input, args: ['nglIconCategory', ] },],
'icon': [{ type: Input, args: ['nglIcon', ] },],
'xPos': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
